input {
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    font-size: 1rem;
}

input[type="submit"] {
    padding: 0.25em 0.75em;
    background-color: $brand-two;
    color: #fff;
    border: 0;
    border-radius: 4px;
    margin: 0 auto;
    display: block;
}


